<template>
    <div class="search">
        <div class="search-box-wrapper">
            <SSearchBox ref="searchBox" @query="onQueryChange"></SSearchBox>
        </div>
        <div class="shortcut-wrapper" ref="shortcutWrapper" v-show="!query">
            <SScroll :refreshDelay="refreshDelay" class="shortcut" ref="shortcut" :data="shortcut">
               <div>
                   <div class="hot-key">
                       <h1 class="title">热门搜索</h1>
                       <ul>
                           <li @click="addQuery(item.k)"
                               class="item"
                               v-for="item in hotKey"
                           >
                               <span>{{item.k}}</span>
                           </li>
                       </ul>
                   </div>
                   <div class="search-history" v-show="searchHistory.length">
                       <h1 class="title">
                           <span class="text">搜索历史</span>
                           <span class="clear" @click="showConfirm">
                            <i class="icon-clear"></i>
                        </span>
                       </h1>
                       <SSearchList @select="addQuery"
                                    @delete="deleteOne"
                                    :searches="searchHistory"
                       ></SSearchList>
                   </div>
               </div>
            </SScroll>
        </div>
        <div class="search-result" ref="searchResult" v-show="query">
            <SSuggest @select="saveSearch" @listScroll="blurInput" :query="query" ref="suggest"></SSuggest>
        </div>
        <SConfirm ref="confirm"
                  text="是否清空所有搜索历史"
                  confirmBtnText="清空"
                  @confirm="deleteAllSHistory"
        ></SConfirm>
        <router-view></router-view>
    </div>
</template>

<script>
    import SSearchBox from '@/components/base/SearchBox'
    import SSuggest from '@/components/search/Suggest'
    import SSearchList from '@/components/base/SearchList'
    import SConfirm from '@/components/base/Confirm'
    import SScroll from '@/components/base/Scroll'
    import {searchMixin} from "js/mixin";
    import {getHotKey} from "api/search";
    import {mapActions,mapGetters} from 'vuex'
    import {playlistMixin} from 'js/mixin'
    import {ERR_OK} from "api/config";

    export default {
        mixins:[playlistMixin,searchMixin],
        name: "Search",
        components:{
            SSearchBox,
            SSuggest,
            SSearchList,
            SConfirm,
            SScroll
        },
        data(){
            return {
                hotKey:[]
            }
        },
        created(){
            this._getHotKey()
        },
        computed:{
            shortcut(){
                return this.hotKey.concat(this.searchHistory)
            },
            ...mapGetters([
                'searchHistory'
            ])
        },
        watch:{
            query(newQuery){
                if(!newQuery){
                    setTimeout(()=>{
                        this.$refs.shortcut.refresh()
                    })
                }
            }
        },
        methods:{
            onHandlePlaylist(playlist){
                const bottom=playlist.length>0?'60px':'';
                this.$refs.shortcutWrapper.style.bottom=bottom;
                this.$refs.searchResult.style.bottom=bottom
                this.$refs.suggest.refresh()
            },
            // 获取关键词
            _getHotKey(){
                getHotKey().then((res)=>{
                    if(res.code===ERR_OK){
                       this.hotKey=res.data.hotkey.slice(0,10);
                    }
                })
            },
            // 清空搜索历史
            deleteAllSHistory(){
                this.clearSearchHistory()
            },
            // 显示弹窗
            showConfirm(){
                this.$refs.confirm.show()
            },
            ...mapActions([
                'clearSearchHistory'
            ]),
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "~stylus/variable"
    @import "~stylus/mixin"

    .search
        .search-box-wrapper
            margin: 20px
        .shortcut-wrapper
            position: fixed
            top: 178px
            bottom: 0
            width: 100%
            .shortcut
                height: 100%
                overflow: hidden
                .hot-key
                    margin: 0 20px 20px 20px
                    .title
                        margin-bottom: 20px
                        font-size: $font-size-medium
                        color: $color-text-l
                    .item
                        display: inline-block
                        padding: 5px 10px
                        margin: 0 20px 10px 0
                        border-radius: 6px
                        background: $color-highlight-background
                        font-size: $font-size-medium
                        color: $color-text-d
                .search-history
                    position: relative
                    margin: 0 20px
                    .title
                        display: flex
                        align-items: center
                        height: 40px
                        font-size: $font-size-medium
                        color: $color-text-l
                        .text
                            flex: 1
                        .clear
                            extend-click()
                            .icon-clear
                                font-size: $font-size-medium
                                color: $color-text-d
        .search-result
            position: fixed
            width: 100%
            top: 178px
            bottom: 0
</style>


